@import "../../../styles/main";

.exchange_wrapper {
  background: $graylight;

  h3 {
    margin-bottom: 40px;
    color: $darkblue;
  }

  header {
    margin: 20px 0;

    h1 {
      color: $graylight;
    }

    .carousel {
      margin-top: 30px;
      margin-bottom: 30px;
    }
  }

  .exchange {
    min-height: calc(100vh - 186px);
    background-color: $white;
    margin-top: 20px;

    .code {
      font-weight: bold;
      font-size: 30px;
      padding: 5px;
      background-color: $darkbluelight;
      display: inline-block;
      color: $brownlight;
    }
    .value {
      font-size: 1.5rem;
      display: flex;
      align-items: center;
      color: $darkblue;
      font-size: 28px;
      padding: 5px;
      border-top: 1px solid $darkbluelight;
      margin: 0;
    }
    .back {
      margin-bottom: 20px;
      text-align: right;
      padding-top: 20px;
    }

    .date {
      display: block;
      font-style: italic;
      padding: 5px;

      border-top: 1px solid $graylight;
    }

    .currency {
      font-size: 22px;
      display: block;
      padding: 10px 0;
    }

    .tab_content {
      height: 400px;
      border: 1px solid;
    }

    &.next {
      height: auto;
      min-height: 360px;
    }
  }

  .rate {
    font-size: 0.85rem;
    font-style: italic;

    &.green {
      color: green;
    }
    &.red {
      color: red;
    }
  }

  .table_min_max {
    margin-top: 40px;
    font-size: 12px;

    @include media-query-sm {
      font-size: 1rem;
    }

    .min {
      color: red;
    }

    .max {
      color: green;
    }

    .date_min_max {
      font-style: italic;
    }
    .description {
      font-size:0.85rem;
      font-style: italic;
    }
  }
  .tab_wrapper {
    margin-top: 20px;

    @include media-query-md {
      margin-top: 0;
    }
  }
}

.descr {
  margin-right: 5px;
  font-size: 1rem;
  font-weight: normal;
}
